import * as React from 'react';
import { Button, ButtonProps, Label } from 'semantic-ui-react';
import * as _get from 'lodash.get';
import { bind } from '../../util';

interface IFileUploadButtonProps {
    onSelect: (file: any) => void;
}

export class FileUploadButton extends React.Component<IFileUploadButtonProps & ButtonProps, {}> {
    private inputControl: any = React.createRef();

    constructor(props) {
        super(props);
    }

    public render() {
        return (
            <div>
                <Button
                    {...this.props}
                    as="label"
                    htmlFor={'uploadFile'}
                />
                <input
                    ref={this.inputControl}
                    hidden
                    id={'uploadFile'}
                    multiple
                    type="file"
                    onChange={this.onChangeFile}
                />
            </div>
        );
    }

    @bind
    public onChangeFile() {
        const {
            onSelect
        } = this.props;

        const file = _get(this.inputControl, 'current.files.0');
        if (onSelect) {
            onSelect(file);
        }
    }
}
