import * as React from 'react';
import { Modal, Form, Header, Button, Grid, TextArea } from 'semantic-ui-react';
import { bind } from '../../util';
import * as _get from 'lodash.get';

interface IConnectIoTCentralDialogState {
    visible: boolean;
    systemName: string;
    systemId: string;
    appUrl: string;
    deviceId: string;
    scopeId: string;
    deviceKey: string;
    templateId: string;
    templateVersion: string;
    dcmid: string;
    iotCentralHubConnectionString: string;
    iotCentralProvisioningStatus: string;
    iotCentralConnectionStatus: string;
    onDialogCompletion: (ok: boolean) => void;
}

export class ConnectIoTCentralDialog extends React.Component<any, IConnectIoTCentralDialogState> {
    constructor(props: any, context?: any) {
        super(props, context);

        this.state = {
            visible: false,
            systemName: '',
            systemId: '',
            appUrl: '',
            deviceId: '',
            scopeId: '',
            deviceKey: '',
            templateId: '',
            templateVersion: '',
            dcmid: '',
            iotCentralHubConnectionString: '',
            iotCentralProvisioningStatus: '',
            iotCentralConnectionStatus: '',
            onDialogCompletion: null
        };
    }

    public doModal(onDialogCompletion: (on: boolean) => void, iotcConfig: any) {
        this.setState({
            visible: true,
            systemName: _get(iotcConfig, 'systemName'),
            systemId: _get(iotcConfig, 'systemId'),
            appUrl: _get(iotcConfig, 'appUrl'),
            deviceId: _get(iotcConfig, 'deviceId'),
            scopeId: _get(iotcConfig, 'scopeId'),
            deviceKey: _get(iotcConfig, 'deviceKey'),
            templateId: _get(iotcConfig, 'templateId'),
            templateVersion: _get(iotcConfig, 'templateVersion'),
            dcmid: _get(iotcConfig, 'dcmid'),
            iotCentralHubConnectionString: _get(iotcConfig, 'iotCentralHubConnectionString'),
            iotCentralProvisioningStatus: _get(iotcConfig, 'iotCentralProvisioningStatus'),
            iotCentralConnectionStatus: _get(iotcConfig, 'iotCentralConnectionStatus'),
            onDialogCompletion
        });
    }

    public render() {
        const {
            visible,
            systemName,
            systemId,
            appUrl,
            deviceId,
            scopeId,
            templateId,
            templateVersion,
            deviceKey,
            dcmid,
            iotCentralHubConnectionString,
            iotCentralProvisioningStatus,
            iotCentralConnectionStatus
        } = this.state;

        const valid = true;

        return (
            <Modal
                open={visible}
                closeOnDimmerClick={false}
                closeOnEscape={false}
                onKeyPress={this.onKeyPress}
            >
                <Header icon="microsoft" content="Azure IoT Central" />
                <Modal.Content>
                    <Modal.Description>
                        <Header>Device Provisioning Information</Header>
                        <Grid>
                            <Grid.Row>
                                <Grid.Column>
                                    <Form>
                                        <Form.Group>
                                            <Form.Input
                                                id="deviceId"
                                                readOnly
                                                label="Device ID"
                                                width={8}
                                                placeholder="Device ID to use for this device"
                                                value={deviceId}
                                            />
                                            <Form.Input
                                                id="deviceKey"
                                                readOnly
                                                label="Device Key"
                                                width={8}
                                                placeholder="Device key produced from your IoT Central app key"
                                                value={deviceKey}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Input
                                                id="scopeId"
                                                readOnly
                                                label="Scope ID"
                                                width={6}
                                                placeholder="Scope ID from your IoT Central administration settings"
                                                value={scopeId}
                                            />
                                            <Form.Input
                                                id="templateId"
                                                readOnly
                                                label="Template ID"
                                                width={5}
                                                placeholder="Template ID from your IoT Central administration settings"
                                                value={templateId}
                                            />
                                            <Form.Input
                                                id="templateVersion"
                                                readOnly
                                                label="Template Version"
                                                width={5}
                                                placeholder="Template version from your IoT Central administration settings"
                                                value={templateVersion}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Input
                                                id="dcmid"
                                                readOnly
                                                label="Device Capability Model ID"
                                                width={16}
                                                placeholder="Device Capability Model ID for this device to use"
                                                value={dcmid}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Input
                                                id="connectionString"
                                                readOnly
                                                label="Connection String"
                                                width={16}
                                                placeholder="IoT Hub connection string return from device provisioning service"
                                                value={iotCentralHubConnectionString}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Input
                                                id="provisioningStatus"
                                                readOnly
                                                label="IoT Central Provisioning Status"
                                                width={16}
                                                placeholder="IoT Central provisioning status"
                                                value={iotCentralProvisioningStatus}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Input
                                                id="connectionStatus"
                                                readOnly
                                                label="IoT Central Connection Status"
                                                width={16}
                                                placeholder="IoT Central connection status"
                                                value={iotCentralConnectionStatus}
                                            />
                                        </Form.Group>
                                    </Form>
                                </Grid.Column>
                            </Grid.Row>
                        </Grid>
                    </Modal.Description>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                        floated="left"
                        href={appUrl}
                    >
                        Go to Azure IoT Central
                    </Button>
                    <Button disabled={!valid} onClick={this.onOk}>OK</Button>
                </Modal.Actions>
            </Modal>
        );
    }

    @bind
    private onKeyPress(e: any) {
        if (e.nativeEvent.key === 'Enter' && this.props.valid) {
            this.props.onOk(e);
        }
    }

    @bind
    private onCancel(e: any) {
        this.setState({
            visible: false
        });
    }

    @bind
    private onOk(e: any) {
        const {
            onDialogCompletion
        } = this.state;

        this.setState({
            visible: false
        });

        onDialogCompletion(true);
    }
}
