import * as React from 'react';
import { Form, Divider } from 'semantic-ui-react';
import { bind } from '../../util';

interface IWowzaPlayerProps {
    license: string;
    sourceUrl: string;
    deviceId: string;
    description: string;
    title: string;
    width?: number;
    height?: number;
    paddingBottom?: number;
    paddingLeft?: number;
    paddingRight?: number;
    paddingTop?: number;
}

const wowzaPlayerUrl = 'https://player.wowza.com/player/latest/wowzaplayer.min.js';

export class WowzaReactPlayer extends React.Component<IWowzaPlayerProps, any> {
    public static defaultProps = {
        license: '',
        sourceUrl: '',
        deviceId: '',
        description: '',
        title: '',
        width: '100%',
        height: 0,
        paddingBottom: '54.25%',
        paddingLeft: 0,
        paddingRight: 0,
        paddingTop: 0
    };

    private wowzaPlayerInternal: any;

    constructor(props: any, context?: any) {
        super(props, context);

        this.wowzaPlayerInternal = null;
    }

    public get wowzaPlayer() {
        return this.wowzaPlayerInternal;
    }

    public async componentDidMount() {
        await this.loadScript(wowzaPlayerUrl);

        const wowzaProps = {
            sourceURL: this.props.sourceUrl,
            audioOnly: false,
            autoPlay: true,
            loop: false,
            mute: true,
            startAtLivePoint: true,
            uiShowBitrateSelector: false,
            uiShowDurationVsTimeRemaining: false,
            uiShowFullscreen: false,
            uiShowPlaybackControls: false,
            uiShowQuickRewind: false,
            uiShowSeekBar: false,
            uiShowVolumeControl: false,
            uiQuickRewindSeconds: false,
            volume: '0',
            withCredentials: false,
            ...this.props
        };

        this.wowzaPlayerInternal = window['WowzaPlayer'].create('wowzaPlayerElement', wowzaProps);
    }

    public destroyPlayer() {
        if (this.wowzaPlayerInternal) {
            this.wowzaPlayerInternal.destroy();
        }

        this.wowzaPlayerInternal = null;
    }

    public stop() {
        if (this.wowzaPlayerInternal) {
            this.wowzaPlayerInternal.pause();
        }
    }

    public play() {
        if (this.wowzaPlayerInternal) {
            this.wowzaPlayerInternal.seek(Number.MAX_SAFE_INTEGER);
            this.wowzaPlayerInternal.play();
        }
    }

    public render() {
        const {
            deviceId,
            width,
            height,
            paddingBottom,
            paddingLeft,
            paddingRight,
            paddingTop
        } = this.props;

        return (
            <div>
                <Form.Group>
                    <Form.Field width="16">
                        {this.wowzaPlayerInternal ? (<label>{deviceId}</label>) : null}
                        <div
                            id="wowzaPlayerElement"
                            style={{
                                width,
                                height,
                                paddingBottom,
                                paddingLeft,
                                paddingRight,
                                paddingTop
                            }}
                        />
                    </Form.Field>
                </Form.Group>
                <Divider />
            </div>
        );
    }

    @bind
    private async loadScript(url: string): Promise<void> {
        return new Promise((resolve, reject) => {
            const script = document.createElement('script');
            script.type = 'text/javascript';
            script.src = url;
            script.async = false;

            script.onload = () => {
                return resolve();
            };

            document.body.appendChild(script);
        });
    }
}
