    @bind
    private async onClickSession(e: any, data: any) {
        const {
            clientStore
        } = this.props;

        if (_get(clientStore, 'deviceConfig.sessionToken')) {
            this.destroyVideoPlayer();

            await clientStore.destroySession();

            this.setState({
                showVideoStream: false
            });
        }
        else {
            await clientStore.createSession();
        }
    }

    @bind
    private async onShowVideoStreamChange(e: any, data: any) {
        const {
            showVideoStream
        } = this.state;

        if (showVideoStream) {
            this.destroyVideoPlayer();
        }

        this.setState({
            showVideoStream: !showVideoStream
        });
    }

    @bind
    private async onSelectNewModel(file: any) {
        const {
            clientStore
        } = this.props;

        clientStore.uploadModel(file);
    }

    @bind
    private async handleVisibilityChange(e: any) {
        const {
            showVideoStream,
            lastShowVideoStreamState
        } = this.state;

        if (document[this.documentHiddenProperty]) {
            this.destroyVideoPlayer();

            this.setState({
                showVideoStream: false,
                lastShowVideoStreamState: showVideoStream
            });
        } else {
            this.setState({
                showVideoStream: lastShowVideoStreamState
            });
        }
    }

    @bind
    private async destroyVideoPlayer() {
        if (this.wowzaVideoPlayer.current) {
            this.wowzaVideoPlayer.current.destroyPlayer();
        }
    }

