import { action, observable, runInAction, toJS } from 'mobx';
import { DataStore, StoreEvents, ErrorTypes } from '.';
import { EventEmitter2 } from 'eventemitter2';
import {
    getAuthTokenApi,
    createSessionApi,
    destroySessionApi,
    setDisplayOutApi,
    getConfigurationApi,
    uploadModelApi,
    resetDeviceApi
} from '../../api/client';
import { bind, forget } from '../../util';
import * as _get from 'lodash.get';
import * as Nes from '@hapi/nes';
import * as base64Converter from 'base64-arraybuffer';
import { blankImage } from '../../../static';

const genericError = `Sorry, an unknown error occurred, try again after rebooting your device`;
const authTokenError = `An error occurred while communicating with the device's service.`;

const serverHealthSubscriptionPath = `/subscription/health`;
const inferenceSubscriptionPath = `/subscription/inference`;
const modelFileUpdateSubscriptionPath = `/subscription/model`;
const createSessionSubscriptionPath = `/subscription/createsession`;
const destroySessionSubscriptionPath = `/subscription/destroysession`;
const updateConfigurationSubscriptionPath = `/subscription/updateconfiguration`;

const emptyInferences = [
    {
        timestamp: Date.now(),
        display_name: 'None',
        id: '0',
        confidence: '0',
        count: 0,
        image: blankImage
    }
];

export class ClientStore implements DataStore {
    public static displayName = 'clientStore';

    @observable
    public loading: boolean = true;

    @observable
    public authToken: string = '';

    @observable
    public deviceConfig = {
        sessionToken: '',
        ipAddresses: {
            cameraIpAddress: '127.0.0.1',
            hostIpAddress: '127.0.0.1'
        },
        rtspVideoUrl: '',
        rtspVamUrl: '',
        resolution: 1,
        resolutions: [],
        encoder: 1,
        encoders: [],
        bitRate: 3,
        bitRates: [],
        frameRate: 1,
        frameRates: [],
        modelFile: '',
        hdmiOutput: false,
        vamProcessing: false,
        wowzaPlayerLicense: '',
        wowzaPlayerVideoSourceUrl: ''
    };

    @observable
    public iotcConfig = {
        systemName: '',
        systemId: '',
        appUrl: '',
        deviceId: '',
        scopeId: '',
        deviceKey: '',
        templateId: '',
        templateVersion: '',
        dcmid: '',
        iotCentralHubConnectionString: ''
    };

    // @observable
    // public inference: any = null;

    // @observable
    // public inferenceImage: any = '';

    @observable
    public inferences = emptyInferences;

    private nesClient: Nes.Client = null;

    public get isProduction() {
        return process.env.NODE_ENV === 'production';
    }

    private _emitter = new EventEmitter2();

    public get emitter() {
        return this._emitter;
    }

    // // @ts-ignore (storeProvider)
    // public initialize(storeProvider: StoreProvider) {
    //     forget(this.subscribeToServerEvents);
    // }

    @action
    public async getAuthToken(): Promise<boolean> {
        if (this.authToken) {
            return true;
        }

        try {
            const response = await getAuthTokenApi();
            if (response.succeeded) {
                runInAction(() => {
                    this.authToken = _get(response, 'body.token') || '';
                });
            }
            else {
                this.emitError('Server error', authTokenError);
            }

            return response.succeeded;
        }
        catch (error) {
            this.emitError('Unknown error', genericError);

            return false;
        }
    }

    @action
    public async createSession(): Promise<boolean> {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            succeeded = await this.getAuthToken();
            if (succeeded) {
                const response = await createSessionApi(this.authToken);
                succeeded = _get(response, 'body.status');

                if (succeeded) {
                    runInAction(() => {
                        this.deviceConfig = _get(response, 'body.body.deviceConfig');
                        this.iotcConfig = _get(response, 'body.body.iotcConfig');
                    });
                }
                else {
                    this.emitError(_get(response, 'body.title'), _get(response, 'body.message'));
                }
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    @action
    public async destroySession() {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            let response;

            await this.unsubscribeToServerEvents();

            succeeded = await this.getAuthToken();
            if (succeeded) {
                response = await destroySessionApi(this.authToken);
                succeeded = _get(response, 'body.status');

                if (succeeded) {
                    runInAction(() => {
                        this.deviceConfig = _get(response, 'body.body.deviceConfig');
                        this.clearInference();
                    });
                }
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    @action
    public async setDisplayOut(switchStatus: boolean) {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            let response;

            succeeded = await this.getAuthToken();
            if (succeeded) {
                response = await setDisplayOutApi(this.authToken, switchStatus);
                succeeded = _get(response, 'body.status');

                if (!succeeded) {
                    this.emitError(_get(response, 'body.title'), _get(response, 'body.message'));
                }

                succeeded = response.succeeded;
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    @action
    public async getConfiguration(): Promise<boolean> {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            let response;

            succeeded = await this.getAuthToken();
            if (succeeded) {
                response = await getConfigurationApi(this.authToken);
                succeeded = _get(response, 'body.status');

                if (succeeded) {
                    runInAction(() => {
                        this.deviceConfig = _get(response, 'body.body.deviceConfig');
                        this.iotcConfig = _get(response, 'body.body.iotcConfig');
                    });

                    await this.subscribeToServerEvents();
                }
                else {
                    this.emitError(_get(response, 'body.title'), _get(response, 'body.message'));
                }

                succeeded = response.succeeded;
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    @action
    public async uploadModel(file: any): Promise<boolean> {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            let response;

            succeeded = await this.getAuthToken();
            if (succeeded) {
                response = await uploadModelApi(this.authToken, file);
                succeeded = response.succeeded;
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    @action
    public async resetDevice(resetAction: string) {
        let succeeded = false;

        runInAction(() => {
            this.loading = true;
        });

        try {
            let response;

            await this.unsubscribeToServerEvents();

            succeeded = await this.getAuthToken();
            if (succeeded) {
                response = await resetDeviceApi(this.authToken, resetAction);
                succeeded = _get(response, 'body.status');

                if (succeeded) {
                    runInAction(() => {
                        this.deviceConfig = _get(response, 'body.body.deviceConfig');
                        this.clearInference();
                    });
                }
            }
        }
        catch (error) {
            this.emitError('Unknown error', genericError);
        }

        runInAction(() => {
            this.loading = false;
        });

        return succeeded;
    }

    private async clearInference() {
        this.inferences = emptyInferences;
    }

    private emitError(title: string, message: string) {
        this.emitter.emit(StoreEvents.Error, {
            result: false,
            type: ErrorTypes.MessageError,
            error: {
                title,
                message
            }
        });
    }

    @bind
    private async subscribeToServerEvents(): Promise<void> {
        try {
            if (!this.nesClient) {
                const hostIpAddress = _get(toJS(this.deviceConfig), 'ipAddresses.hostIpAddress') || '127.0.0.1';
                this.nesClient = new Nes.Client(`ws://${hostIpAddress}:9010`);
                await this.nesClient.connect();

                await this.nesClient.subscribe(serverHealthSubscriptionPath, this.handleServerHealthSubscription);
                await this.nesClient.subscribe(inferenceSubscriptionPath, this.handleInferenceSubscription);
                await this.nesClient.subscribe(modelFileUpdateSubscriptionPath, this.handleModelFileUpdateSubscription);
                await this.nesClient.subscribe(createSessionSubscriptionPath, this.handleCreateSessionSubscriptionPath);
                await this.nesClient.subscribe(destroySessionSubscriptionPath, this.handleDestroySessionSubscriptionPath);
                await this.nesClient.subscribe(updateConfigurationSubscriptionPath, this.handleUpdateConfigurationSubscriptionPath);
            }
        }
        catch (ex) {
            // eat the exception
        }
    }

    @bind
    private async unsubscribeToServerEvents(): Promise<void> {
        try {
            if (this.nesClient) {
                await this.nesClient.unsubscribe(serverHealthSubscriptionPath, null);
                await this.nesClient.unsubscribe(inferenceSubscriptionPath, null);
                await this.nesClient.unsubscribe(modelFileUpdateSubscriptionPath, null);
                await this.nesClient.unsubscribe(createSessionSubscriptionPath, null);
                await this.nesClient.unsubscribe(destroySessionSubscriptionPath, null);
                await this.nesClient.unsubscribe(updateConfigurationSubscriptionPath, null);

                this.nesClient = null;
            }
        }
        catch (ex) {
            // eat the exception
        }
    }

    // @ts-ignore (flags)
    @bind
    private handleServerHealthSubscription(health: any, flags: any) {
        return;
    }

    // @ts-ignore (flags)
    @bind
    private handleInferenceSubscription(inference: any, flags: any) {
        let jpegDataBase64 = '';

        try {
            const jpegByteArray = _get(inference, 'imageData');
            const jpegData = new Uint8Array(jpegByteArray.data);

            jpegDataBase64 = base64Converter.encode(jpegData);
        }
        catch (ex) {
            // eat the exception
            jpegDataBase64 = '';
        }

        const inferences = inference.inference.inferences.map((inferenceItem) => {
            return {
                timestamp: inference.inference.timestamp,
                display_name: inferenceItem.display_name,
                id: inferenceItem.id,
                confidence: inferenceItem.confidence,
                count: inferenceItem.count,
                image: jpegDataBase64 ? `data:image/png;base64, ${jpegDataBase64}` : ''
            };
        });

        const updatedInferences = inferences.concat(toJS(this.inferences));
        updatedInferences.splice(3);

        runInAction(() => {
            this.inferences = updatedInferences;
        });
    }

    // @ts-ignore (flags)
    @bind
    private handleModelFileUpdateSubscription(modelFile: string, flags: any) {
        runInAction(() => {
            this.deviceConfig = {
                ...this.deviceConfig,
                modelFile
            };
        });
    }

    // @ts-ignore (flags)
    @bind
    private handleCreateSessionSubscriptionPath(event: any, flags: any) {
        forget(this.getConfiguration);
    }

    // @ts-ignore (flags)
    @bind
    private handleDestroySessionSubscriptionPath(event: any, flags: any) {
        return;
    }

    // @ts-ignore (flags)
    @bind
    private handleUpdateConfigurationSubscriptionPath(event: any, flags: any) {
        forget(this.getConfiguration);
    }
}
