import * as React from 'react';
import { Button, Modal, Form, Header, Segment, Label, Image } from 'semantic-ui-react';
import { ErrorStore } from '../stores';
import { ClientLogo50 } from '../../../static';
import { observer, inject } from 'mobx-react';
import { bind } from '../../util';

interface IErrorDialogProps {
    errorStore?: ErrorStore;
}

@inject('errorStore') @observer
export class ErrorDialog extends React.Component<IErrorDialogProps, any> {
    public render() {
        const {
            errorStore
        } = this.props;

        return (
            <Modal
                dimmer
                centered={false}
                closeOnDimmerClick={false}
                size="small"
                open={errorStore.shouldShow}
            >
                <Header icon="warning sign" content="Something went wrong" />
                <Modal.Content image>
                    <Image wrapped size="mini" src={ClientLogo50} />
                    <Modal.Description>
                        <Header>{errorStore.title}</Header>
                        {errorStore.message}
                    </Modal.Description>
                </Modal.Content>
                <Modal.Actions>
                    <Button color="green" onClick={this.dismiss}>Close</Button>
                </Modal.Actions>
            </Modal>
        );
    }

    @bind
    private dismiss() {
        this.props.errorStore.shouldShow = false;
    }
}
