import * as React from 'react';
import { Container, Menu, Grid, Image, Segment, Header, List, Divider, Dropdown, Icon } from 'semantic-ui-react';
import { ErrorDialog } from './ErrorDialog';
import { Switch, Route, Redirect } from 'react-router-dom';
import { ClientStore } from '../stores';
import { HomePage } from '../pages/HomePage';
import { ClientPage } from '../pages/client/ClientPage';
import { CustomVisionLogo50, IoTCentralLogo50, MicrosoftLogo } from '../../../static';
import { ConnectIoTCentralDialog } from '../components/ConnectIoTCentralDialog';
import { bind } from '../../util';
import { inject } from 'mobx-react';
import * as _get from 'lodash.get';

interface IAppProps {
    clientStore: ClientStore;
    location: any;
}

@inject('clientStore', 'errorStore')
export class AppComponent extends React.Component<IAppProps, {}> {
    private connectIoTCentralDialog: ConnectIoTCentralDialog;

    public render() {
        const {
            location
        } = this.props;

        return (
            <div>
                <Menu fixed="top" inverted>
                    <Container>
                        <Menu.Item href="https://www.customvision.ai/">
                            <Image size="mini" src={CustomVisionLogo50} style={{ marginRight: '1.5em' }} />
                            Microsoft Custom Vision
                        </Menu.Item>
                        <Menu.Item onClick={this.onClickConnectIoTCentral}>
                            <Image size="mini" src={IoTCentralLogo50} style={{ marginRight: '1.5em' }} />
                            Azure IoT Central
                        </Menu.Item>
                    </Container >
                </Menu>

                <Container style={{ marginTop: '7em' }}>
                    <Grid>
                        <Grid.Row>
                            <Grid.Column>
                                <Switch>
                                    <Route exact path="/" component={HomePage} />
                                    <Route path="/client" component={ClientPage} />
                                    <Redirect from={location.pathname} to="/" />
                                    {this.props.children}
                                </Switch>
                                <ErrorDialog />
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>

                <Segment inverted vertical style={{ margin: '5em 0em 0em', padding: '5em 0em' }}>
                    <Container textAlign="center">
                        <Grid divided inverted stackable>
                            <Grid.Column width={3}>
                                <Header inverted as="h4" content="Group 1" />
                                <List link inverted>
                                    <List.Item as="a">Link One</List.Item>
                                    <List.Item as="a">Link Two</List.Item>
                                    <List.Item as="a">Link Three</List.Item>
                                    <List.Item as="a">Link Four</List.Item>
                                </List>
                            </Grid.Column>
                            <Grid.Column width={3}>
                                <Header inverted as="h4" content="Group 2" />
                                <List link inverted>
                                    <List.Item as="a">Link One</List.Item>
                                    <List.Item as="a">Link Two</List.Item>
                                    <List.Item as="a">Link Three</List.Item>
                                    <List.Item as="a">Link Four</List.Item>
                                </List>
                            </Grid.Column>
                            <Grid.Column width={3}>
                                <Header inverted as="h4" content="Group 3" />
                                <List link inverted>
                                    <List.Item as="a">Link One</List.Item>
                                    <List.Item as="a">Link Two</List.Item>
                                    <List.Item as="a">Link Three</List.Item>
                                    <List.Item as="a">Link Four</List.Item>
                                </List>
                            </Grid.Column>
                            <Grid.Column width={7}>
                                <Header inverted as="h4" content="Visual Intelligence Made Easy" />
                                <p>
                                    Easily customize your own state-of-the-art computer vision models that fit perfectly with your
                                    unique use case. Just bring a few examples of labeled images and let Custom Vision do the hard work.
                                </p>
                            </Grid.Column>
                        </Grid>

                        <Divider inverted section />
                        <Image centered size="mini" src={MicrosoftLogo} />
                        <List horizontal inverted divided link size="small">
                            <List.Item>© Microsoft 2019</List.Item>
                            <List.Item as="a" href="#">Site Map</List.Item>
                            <List.Item as="a" href="#">Contact Us</List.Item>
                            <List.Item as="a" href="#">Terms and Conditions</List.Item>
                            <List.Item as="a" href="#">Privacy Policy</List.Item>
                        </List>
                    </Container>
                </Segment>
                <ConnectIoTCentralDialog ref={r => this.connectIoTCentralDialog = r} />
            </div>
        );
    }

    @bind
    private async onClickConnectIoTCentral(e: any) {
        const {
            clientStore
        } = this.props;

        this.connectIoTCentralDialog.doModal(this.connectIoTCentralDialogCompletion, _get(clientStore, 'iotcConfig'));
    }

    @bind
    private connectIoTCentralDialogCompletion(ok: boolean) {
        return;
    }
}
