export function uploadModelApi(authToken: string, file: any): Promise<FetchResponse> {
    const formData = new FormData();
    formData.append('model', file, file.name);

    return fetchHelper(makeUrl(`/api/v1/client/model`),
        {
            method: 'POST',
            credentials: 'include',
            headers: {
                // 'Content-Type': 'multipart/form-data',
                Authorization: authToken
            },
            body: formData
        });
}

export function resetDeviceApi(authToken: string, resetAction: string): Promise<FetchResponse> {
    return fetchHelper(makeUrl(`/api/v1/client/reset`),
        {
            method: 'POST',
            credentials: 'include',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': authToken
            },
            body: JSON.stringify({
                action: resetAction
            })
        });
}
